#section-principale-404 {
  display: flex;
  flex-direction: column;
  align-items: center;
}



@media (max-width: 576px) {
  #section-principale-404 h1 {
    font-size: 96px;
  }
  #section-principale-404 p {
    display: flex;
    flex-direction: column;
    font-size: 18px;
    align-items: center;
  }
  #section-principale-404 a {
    font-size: 14px;
  }
}
@media (min-width: 992px) {
  #section-principale-404 h1 {
    font-size: 288px;
  }
  #section-principale-404 p {
    display: block;
    font-size: 36px;
  }
  #section-principale-404 a {
    font-size: 18px;
  }
}/*# sourceMappingURL=Error.css.map */