form {
    max-width: 400px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f9f9f9;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  label {
    font-weight: bold;
    display: block;
    margin-bottom: 8px;
  }
  
  input[type='email'],
  textarea {
    width: 92%;
    padding: 10px;
    margin-bottom: 15px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
  }
  
  textarea {
    min-height: 150px;
  }
  
  button[type='submit'] {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 12px 20px;
    font-size: 16px;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s ease;
  }
  
  button[type='submit']:hover {
    background-color: #0056b3;
  }

  .envoi {
    text-align: center; 
  }
  
  .error {
    color: #dc3545;
    margin-bottom: 15px;
    font-size: 14px;
  }
  
  @media (max-width: 600px) {
    form {
      padding: 15px;
    }
  
    button[type='submit'] {
      width: 100%;
    }
  }
  
  .confirmation-message {
    border: 2px solid #4caf50; /* Couleur verte pour le cadre */
    background-color: #f1f1f1; /* Couleur de fond gris clair */
    padding: 10px;
    margin-top: 10px; /* Marge supérieure pour l'espacement */
  }
  